body {
    background-color: #ffcc80;
    color: rgba(0, 0, 0, 0.8);
}

.grid-container {
    display: grid;
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 100px 100px 100px;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 50px;
}

/* .grid-container:before,
.grid-container:after {
    position: absolute;
    content: " ";
    left: 50px;
    border-left: 5px solid red;
} */


.grid-item {
  background-color: #ffcc80;
  border: 2px solid rgba(0, 0, 0, 0.8);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.grid-item:nth-child(n + 7) {
  border-bottom: 0;
}
.grid-item:nth-child(-n + 3) {
  border-top: 0;
}
.grid-item:nth-child(3n) {
  border-right: 0;
}
.grid-item:nth-child(3n + 1) {
  border-left: 0;
}

.grid-item:hover {
  opacity: 0.5;
}

.winContainer {
  font-size: 4em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.winner {
  display: block;
  font-weight: 600;
  color: #da3e52;
}

.loser {
  font-weight: 200;
  opacity: 0.5;
  position: relative;
}

.fade-in {
  opacity: 1;
  -webkit-animation-name: fadeInOpacity;
          animation-name: fadeInOpacity;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
          animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}


.fade-in-winner {
  -webkit-animation-name: fadeInWinner;
          animation-name: fadeInWinner;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}


.grid-item.rowline:after {
  z-index:1;
  border-top: 5px solid #ff5722;
  content: " ";
  width: 300px;
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  -webkit-animation-name: drawRowLine;
          animation-name: drawRowLine;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.grid-item.colline:after {
  z-index:1;
  border-left: 5px solid #ff5722;
  content: " ";
  height: 300px;
  position: absolute;
  margin: 0 auto;
  -webkit-animation-name: drawColLine;
          animation-name: drawColLine;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.grid-item.backDiagLine:after {
  z-index:1;
  border-left: 5px solid #ff5722;
  content: " ";
  height: 300px;
  position: absolute;
  margin: 0 auto;
  -webkit-transform: rotateZ(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotateZ(-45deg);
  -webkit-animation-name: drawColLine;
          animation-name: drawColLine;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.grid-item.fwdDiagLine:after {
  z-index:1;
  border-left: 5px solid #ff5722;
  content: " ";
  height: 300px;
  position: absolute;
  margin: 0 auto;
  -webkit-transform: rotateZ(45deg);
      -ms-transform: rotate(45deg);
          transform: rotateZ(45deg);
  -webkit-animation-name: drawColLine;
          animation-name: drawColLine;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

