  animation-name: gridAnimation;
  animation-fill-mode: forwards;
  animation-duration: 1s;
} */

@-webkit-keyframes gridAnimation {
 0% {
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
 }  
 100% {

 }
}

@keyframes gridAnimation {
 0% {
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
 }  
 100% {

 }
}



@-webkit-keyframes fadeInWinner {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    /* transform: translateY(0%); */
  }
}



@keyframes fadeInWinner {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    /* transform: translateY(0%); */
  }
}

@-webkit-keyframes drawColLine {
  0% {
    height:0px;
  }
  100% {
    height:300px;
  }
}

@keyframes drawColLine {
  0% {
    height:0px;
  }
  100% {
    height:300px;
  }
}

@-webkit-keyframes drawRowLine {
  0% {
    width:0px;
  }
  
  100% {
    width: 300px;
  }
}

@keyframes drawRowLine {
  0% {
    width:0px;
  }
  
  100% {
    width: 300px;
  }
}


@-webkit-keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    font-size: 3em;
  }
}


@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    font-size: 3em;
  }
}



